class TableSpoiler {
  constructor() {
    this.showMore = document.querySelector('.plan-compare__show-more');
    this.spoiler = document.querySelector('.plan-compare__spoiler');
  }

  init() {
    if (!this.showMore || !this.spoiler) return;

    this.showMore.addEventListener('click', (evt) => {
      this.showMore.classList.add('plan-compare__show-more--open');
      this.spoiler.classList.add('plan-compare__spoiler--open');
    })
  }
}

export default TableSpoiler;
