class FAQ {
  constructor() {
    this.QUESTION_CLASS = 'questions__description';

    const questionElements = document.querySelectorAll(`.${this.QUESTION_CLASS}`);

    if (questionElements.length === 0) return;
    this.questions = Array.from(questionElements);
  }

  init() {
    if(!this.questions) return;

    this.questions.forEach((question) => {
      question.addEventListener('click', (evt) => {
        this.toggle(question);
      });
    });
  }

  toggle(question) {
    if (document.documentElement.clientWidth >= 1000) {
      this.questions.forEach((question) => {
        question.classList.remove(`${this.QUESTION_CLASS}--open`)
      });
    }
    question.classList.toggle(`${this.QUESTION_CLASS}--open`);
  }
}

export default FAQ;
