export default class TinkoffOrderPopup {
  constructor() {
    this._escKeyHandler = this._escKeyHandler.bind(this);
    this._submitHandler = this._submitHandler.bind(this);
    this._popup = document.querySelector('.tinkoff-popup');
    this._closeBtn = document.querySelector('.tinkoff-popup__close-btn');

    if (this._popup && this._closeBtn) {
      this._init();
    }
  }
  
  _init() {
    this._form = this._popup.querySelector(`#tinkoff-order`);
    this._priceInput = this._popup
      .querySelector('.tinkoffPayRow[name="amount"]');
    this._descriptionInput = this._popup
      .querySelector('.tinkoffPayRow[name="description"]');

    document.addEventListener('click', (evt) => {
      if (evt.target.classList.contains('buy-button')) {
        evt.preventDefault();
        const button = evt.target;
        this._priceInput.value = button.dataset.price.toString();
        this._descriptionInput.value = button.dataset.title;
        this._openPopup();
      }
    });

    this._form.addEventListener(`submit`, this._submitHandler);

    this._closeBtn.addEventListener('click', (evt) => {
      evt.preventDefault();
      this._closePopup();
    })
  }

  _openPopup() {
    this._popup.classList.add('tinkoff-popup--open');
    document.addEventListener('keydown', this._escKeyHandler);
  }

  _closePopup() {
    this._popup.classList.remove('tinkoff-popup--open');
    document.removeEventListener('keydown', this._escKeyHandler);
  }

  _escKeyHandler(evt) {
    if (evt.key === `Escape` || evt.key === `Esc`) {
      this.closePopup();
    }
  }

  _submitHandler(evt) {
    evt.preventDefault();

    console.log(this._priceInput.value);

    this._emailInput = this._popup
      .querySelector('.tinkoffPayRow[name="email"]');
    this._phoneInput = this._popup
      .querySelector('.tinkoffPayRow[name="phone"]');
    this._receiptInput = this._popup
      .querySelector('.tinkoffPayRow[name="receipt"]');

    const receiptData = {
      "Email": this._emailInput.value,
      "Phone": this._phoneInput.value,
      // "EmailCompany": "b@test.ru",
      "Taxation": "usn_income",
      "Items": [
        {
          "Name": this._descriptionInput.value,
          "Price": this._priceInput.value * 100,
          "Quantity": 1.00,
          "Amount": this._priceInput.value * 100,
          // "PaymentMethod": "full_prepayment",
          "PaymentObject": "service",
          "Tax": "none",
          // "Ean13": "0123456789"
        }
      ]
    };

    console.log(receiptData.Items);

    this._receiptInput.value = JSON.stringify(receiptData);

    window.pay(this._form);

    return false;
    // this._form.submit();
  }
}
